body {
  color: var(--quicksilver-900);
  font-size: 1rem;
  line-height: 1.75;
}

[class~=lead] {
  color: var(--quicksilver-600);
  font-size: 1.25em;
  line-height: 1.6;
  margin-top: 1.2em;
  margin-bottom: 1.2em;
}

a {
  color: var(--bigWaves-800);
  text-decoration: underline;
  font-weight: 500;
}

strong {
  color: var(--bigWaves-800);
  font-weight: 600;
}

ol[type=A] {
  --list-counter-style: upper-alpha;
}

ol[type=a] {
  --list-counter-style: lower-alpha;
}

ol[type="A s"] {
  --list-counter-style: upper-alpha;
}

ol[type="a s"] {
  --list-counter-style: lower-alpha;
}

ol[type=I] {
  --list-counter-style: upper-roman;
}

ol[type=i] {
  --list-counter-style: lower-roman;
}

ol[type="I s"] {
  --list-counter-style: upper-roman;
}

ol[type="i s"] {
  --list-counter-style: lower-roman;
}

ol[type="1"] {
  --list-counter-style: decimal;
}

ol > li {
  position: relative;
  padding-left: 1.75em;
}

ol > li::before {
  content: counter(list-item, var(--list-counter-style, decimal)) ".";
  position: absolute;
  font-weight: 400;
  color: var(--bigWaves-500);
  left: 0;
}

ul > li {
  position: relative;
  padding-left: 1.75em;
}

ul > li::before {
  content: "";
  position: absolute;
  background-color: var(--quicksilver-200);
  border-radius: 50%;
  width: 0.375em;
  height: 0.375em;
  top: calc(0.875em - 0.1875em);
  left: 0.25em;
}

hr {
  border-color: var(--bigWaves-100);
  border-top-width: 1px;
  margin-top: 3em;
  margin-bottom: 3em;
}

blockquote {
  font-weight: 500;
  font-style: italic;
  color: var(--bigWaves-800);
  border-left-width: 0.25rem;
  border-left-color: var(--bigWaves-100);
  quotes: "“" "”" "‘" "’";
  margin-top: 1.6em;
  margin-bottom: 1.6em;
  padding-left: 1em;
}

blockquote p:first-of-type::before {
  content: open-quote;
}

blockquote p:last-of-type::after {
  content: close-quote;
}

h1 {
  color: var(--quicksilver-800);
  font-weight: 600;
  font-size: 2.25em;
  margin-top: 0;
  margin-bottom: 0.8888889em;
  line-height: 1.1111111;
}

h2 {
  color: var(--quicksilver-700);
  font-weight: 600;
  font-size: 1.5em;
  margin-top: 2em;
  margin-bottom: 1em;
  line-height: 1.3333333;
}

h3 {
  color: var(--quicksilver-600);
  font-weight: 600;
  font-size: 1.25em;
  margin-top: 1.6em;
  margin-bottom: 0.6em;
  line-height: 1.6;
}

h4 {
  color: var(--quicksilver-600);
  font-weight: 600;
  margin-top: 1.5em;
  margin-bottom: 0.5em;
  line-height: 1.5;
}

figure figcaption {
  color: var(--bigWaves-500);
  font-size: 0.875em;
  line-height: 1.4285714;
  margin-top: 0.8571429em;
}

code {
  color: var(--bigWaves-800);
  font-weight: 600;
  font-size: 0.875em;
}

code::before {
  content: "`";
}

code::after {
  content: "`";
}

a code {
  color: var(--bigWaves-800);
}

pre {
  color: var(--bigWaves-100);
  background-color: var(--bigWaves-800);
  overflow-x: auto;
  font-size: 0.875em;
  line-height: 1.7142857;
  margin-top: 1.7142857em;
  margin-bottom: 1.7142857em;
  border-radius: 0.375rem;
  padding-top: 0.8571429em;
  padding-right: 1.1428571em;
  padding-bottom: 0.8571429em;
  padding-left: 1.1428571em;
}

pre code {
  background-color: transparent;
  border-width: 0;
  border-radius: 0;
  padding: 0;
  font-weight: 400;
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
}

pre code::before {
  content: none;
}

pre code::after {
  content: none;
}

table {
  width: 100%;
  table-layout: auto;
  text-align: left;
  margin-top: 2em;
  margin-bottom: 2em;
  font-size: 0.875em;
  line-height: 1.7142857;
}

thead {
  color: var(--bigWaves-800);
  font-weight: 600;
  border-bottom-width: 1px;
  border-bottom-color: var(--bigWaves-200);
}

thead th {
  vertical-align: bottom;
  padding-right: 0.5714286em;
  padding-bottom: 0.5714286em;
  padding-left: 0.5714286em;
}

tbody tr {
  border-bottom-width: 1px;
  border-bottom-color: var(--bigWaves-100);
}

tbody tr:last-child {
  border-bottom-width: 0;
}

tbody td {
  vertical-align: top;
  padding-top: 0.5714286em;
  padding-right: 0.5714286em;
  padding-bottom: 0.5714286em;
  padding-left: 0.5714286em;
}

p {
  margin-top: 1.25em;
  margin-bottom: 1.25em;
}

img {
  margin-top: 2em;
  margin-bottom: 2em;
}

video {
  margin-top: 2em;
  margin-bottom: 2em;
}

figure {
  margin-top: 2em;
  margin-bottom: 2em;
}

figure > * {
  margin-top: 0;
  margin-bottom: 0;
}

h2 code {
  font-size: 0.875em;
}

h3 code {
  font-size: 0.9em;
}

ol {
  margin-top: 1.25em;
  margin-bottom: 1.25em;
}

ul {
  margin-top: 1.25em;
  margin-bottom: 1.25em;
}

li {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}

> ul > li p {
  margin-top: 0.75em;
  margin-bottom: 0.75em;
}

> ul > li > *:first-child {
  margin-top: 1.25em;
}

> ul > li > *:last-child {
  margin-bottom: 1.25em;
}

> ol > li > *:first-child {
  margin-top: 1.25em;
}

> ol > li > *:last-child {
  margin-bottom: 1.25em;
}

ul ul,
ul ol,
ol ul,
ol ol {
  margin-top: 0.75em;
  margin-bottom: 0.75em;
}

hr + * {
  margin-top: 0;
}

h2 + * {
  margin-top: 0;
}

h3 + * {
  margin-top: 0;
}

h4 + * {
  margin-top: 0;
}

thead th:first-child {
  padding-left: 0;
}

thead th:last-child {
  padding-right: 0;
}

tbody td:first-child {
  padding-left: 0;
}

tbody td:last-child {
  padding-right: 0;
}

> :first-child {
  margin-top: 0;
}

> :last-child {
  margin-bottom: 0;
}

h5 {
  color: var(--quicksilver-600);
  font-weight: 600;
}

h6 {
  color: var(--quicksilver-600);
  font-weight: 600;
}

.btn {
  @apply no-underline inline-block px-6 py-4 rounded-sm transform transition bg-goldRush-200 hover:bg-goldRush-100 hover:text-goldRush-900 hover:-translate-y-0.5 focus:ring-goldRush-200 focus:ring-opacity-50 focus:outline-none focus:ring focus:ring-offset-2 active:bg-goldRush-300 uppercase tracking-wider font-semibold text-lg text-center text-goldRush-800 shadow-lg w-full;
}

header .nav-primary {
  @apply md:flex md:flex-row items-center hidden;
